/* All container */
.container {
    display: grid;
    grid-template-rows: 75vh 15vh;
    grid-template-columns: 75vh;

    width: fit-content;
    height: fit-content;

    align-items: center;
    justify-content: center;
}

/* Actual table */
.my-table {
    display: grid;
    grid-template-columns: repeat(3, 15vh);
    grid-template-rows: repeat(3, 15vh);

    justify-self: center;
}

/* Cells */
.my-cell {
    display: flex;
    justify-content: center;
    align-items: center;
}

.idx-0,
.idx-1,
.idx-3,
.idx-4,
.idx-6,
.idx-7 {
    border-right: 1px solid white;
}

.idx-0,
.idx-1,
.idx-2,
.idx-5,
.idx-3,
.idx-4 {
    border-bottom: 1px solid white;
}

/* Inside cells*/
.empty {
    width: 0vh;
    height: 0vh;
}

.circle {
    width: 10vh;
    height: 10vh;

    border-radius: 50%;
    border: 3px solid white;
}


.cross {
    display: flex;
    align-items: center;
    justify-content: center;

    user-select: none;

    width: 10vh;
    height: 10vh;

    color: white;
    font-size: 10vh;
}

/* Restart button */
.btn-restart {
    display: flex;
    align-items: center;
    justify-content: center;

    margin: 0 5vh 0 5vh;
}

.btn-restart span {
    margin-right: 1vh;
}