.winner-0deg {
    position: absolute;
    background-color: red;
    width: 15vh;
    height: 1vh;
}

.winner-45deg {
    position: absolute;
    background-color: red;
    width: 25vh;
    height: 1vh;

    transform: rotate(45deg);
}

.winner-90deg {
    position: absolute;
    background-color: red;
    width: 15vh;
    height: 1vh;

    transform: rotate(90deg);
}

.winner-135deg {
    position: absolute;
    background-color: red;
    width: 25vh;
    height: 1vh;

    transform: rotate(135deg);
}